<template>
  <div class="component_example">
    <h1>Input </h1>
    <h2></h2>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-input placeholder="" type="text" v-model="input1"></zoehis-input>
        <zoehis-input placeholder="" type="textarea" v-model="textarea1"></zoehis-input>
      </div>
      <div class="item-detail">
        <div class="item-code"><pre><code>
        &lt;zoehis-input placeholder=&quot;&quot; type=&quot;text&quot; v-model=&quot;input1&quot;&gt;&lt;/zoehis-input&gt;
        &lt;zoehis-input placeholder=&quot;&quot; type=&quot;textarea&quot; v-model=&quot;textarea1&quot;&gt;&lt;/zoehis-input&gt;
        &lt;script&gt;
          data () {
            return {
              input1:"",
              textarea1:"",
            }
          }
        &lt;script&gt;
        </code></pre>
        </div>
        <div class="item-desc">
          type2text/textarea/<br>
          v-model='value'/<br>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>/</h4>
      <div class="item-content">
        <zoehis-input disabled icon="z_delete_normal" type="text" v-model="input3"></zoehis-input>
        <zoehis-input readonly :close="true" type="textarea" v-model="textarea3"></zoehis-input>
      </div>
      <div class="item-detail">
        <div class="item-code"><pre><code>
            &lt;zoehis-input disabled icon=&quot;z_delete_normal&quot; type=&quot;text&quot;  v-model=&quot;input3&quot;&gt;&lt;/zoehis-input&gt;
            &lt;zoehis-input readonly :close=&quot;true&quot; type=&quot;textarea&quot;  v-model=&quot;textarea3&quot;&gt;&lt;/zoehis-input&gt;
        </code></pre></div>
        <div class="item-desc">
          disabled input <br>
          readonly inputXXclearreadonlyX
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-input placeholder="" type="text" v-model="input4" ref="inputSetGet"></zoehis-input>
        <zoehis-button @clickenter="clickSetValue"></zoehis-button><zoehis-button @clickenter="clickGetValue"></zoehis-button><input placeholder="" type="text" v-model="setGetValue">
      </div>
      <div class="item-detail">
      <div class="item-code"><pre><code>
      &lt;zoehis-input placeholder=&quot;&quot; type=&quot;text&quot; v-model=&quot;input1&quot; ref=&quot;inputSetGet&quot;&gt;&lt;/zoehis-input&gt;
      &lt;script&gt;
        clickSetValue:function(){
          this.$refs.inputSetGet.setValue(this.setGetValue);
        },
        clickGetValue:function(){
          this.setGetValue=this.$refs.inputSetGet.getValue();
        },
      &lt;script&gt;
      </code></pre></div>
        <div class="item-desc">
          ref,this.$refs.inputGetSet
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-input placeholder="" :close="true" type="text" v-model="input5"></zoehis-input>
        <zoehis-input placeholder="" icon="z_delete_normal" type="text" v-model="textarea5"></zoehis-input>
      </div>
      <div class="item-detail">
        <div class="item-code"><pre><code>
        &lt;zoehis-input placeholder=&quot;&quot; :close=&quot;true&quot; type=&quot;text&quot; v-model=&quot;input5&quot;&gt;&lt;/zoehis-input&gt;
        &lt;zoehis-input placeholder=&quot;&quot; icon=&quot;z_delete_normal&quot; type=&quot;text&quot; v-model=&quot;textarea5&quot;&gt;&lt;/zoehis-input&gt;
        </code></pre></div>
        <div class="item-desc">
          :close"XX"
          icon
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <table width="100%">
        <thead>
        <tr>
          <th></th><th></th><th></th><th></th><th></th>
        </tr>
        </thead>
        <tbody>
        <tr><td>:type</td><td></td><td>string</td><td>text/textarea</td><td>text</td></tr>
        <tr><td>v-model='value'</td><td>*</td><td>-</td><td>-</td><td>-</td></tr>
        <tr><td>close</td><td></td><td>boolean</td><td>true/false</td><td>false</td></tr>
        <tr><td>icon</td><td>,closeclose</td><td>string</td><td>-</td><td>-</td></tr>
        <tr><td>disabled</td><td></td><td>boolean</td><td>true/false</td><td>false</td></tr>
        <tr><td>readonly</td><td></td><td>boolean</td><td>true/false</td><td>false</td></tr>
        <tr><td>clearreadonly</td><td>X</td><td>boolean</td><td>true/false</td><td>false</td></tr>
        <tr><td>placeholder</td><td></td><td>string</td><td>-</td><td>-</td></tr>
        <tr><td>width</td><td>,auto</td><td>number/string</td><td>-</td><td>115</td></tr>
        <tr><td>height</td><td>,auto</td><td>number/string</td><td>-</td><td>-</td></tr>
        <tr><td>@focus</td><td>focus</td><td>-</td><td>-</td><td>-</td></tr>
        <tr><td>@blur</td><td>blur</td><td>-</td><td>-</td><td>-</td></tr>
        <tr><td>@clear</td><td>clear</td><td>-</td><td>-</td><td>-</td></tr>
        <tr><td>@clickicon</td><td></td><td>-</td><td>-</td><td>-</td></tr>
        <tr><td>getValue()</td><td></td><td>-</td><td>-</td><td>-</td></tr>
        <tr><td>setValue()</td><td></td><td>-</td><td>-</td><td>-</td></tr>
        <tr><td>focus()</td><td></td><td>-</td><td>-</td><td>-</td></tr>
        </tbody>
      </table>
      <div style="clear: both"></div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'input',
    data () {
      return {
        input1:"",
        textarea1:"",
        input2:"",
        textarea2:"",
        input3:"",
        textarea3:"textarea",
        input5:"input",
        textarea5:"textarea",
        input4:"",
        setGetValue:"",
        testt:'10%'
      }
    },
    methods:{
      clickSetValue:function(){
        this.$refs.inputSetGet.setValue(this.setGetValue);
      },
      clickGetValue:function(){
        this.setGetValue=this.$refs.inputSetGet.getValue();
      }
    },
    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }
</script>



