<template>
  <div class="component_example">
    <h1>Button </h1>
    <h2></h2>
    <div class="item">
      <h4></h4>
      <div class="item-content" id="testtest">
        <zoehis-button type="features" icon="z_delete_normal" @clickenter="myClickEnterEve"></zoehis-button>
        <zoehis-button type="features"></zoehis-button><zoehis-button type="features" :line="false"></zoehis-button>
        <zoehis-button></zoehis-button><zoehis-button type="primary"></zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-button type=&quot;features&quot; icon=&quot;z_delete_normal&quot; @clickenter=&quot;myClickEnterEve&quot;&gt;&lt;/zoehis-button&gt;
            &lt;zoehis-button type=&quot;features&quot;&gt;&lt;/zoehis-button&gt;
            &lt;zoehis-button type=&quot;features&quot; :line=&quot;false&quot;&gt;&lt;/zoehis-button&gt;
            &lt;zoehis-button&gt;&lt;/zoehis-button&gt;
            &lt;zoehis-button type=&quot;primary&quot;&gt;&lt;/zoehis-button&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          type2default/primary/features//<br>
          icon<br>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button type="features" :disabled="false" icon="z_delete_normal">()</zoehis-button>
        <zoehis-button type="features" :disabled="true" icon="z_delete_normal">()</zoehis-button>
        <zoehis-button :disabled="false"></zoehis-button><zoehis-button type="primary" :disabled="true"></zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-button type="primary" :disabled="true" &gt;&lt;/zoehis-button&gt;<br/>
            &lt;zoehis-button :disabled="true" &gt;&lt;/zoehis-button&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          disabled2true/false/ <br>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button type="primary"></zoehis-button><zoehis-button type="primary" size="small"></zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-button type="primary"  &gt;&lt;/zoehis-button&gt;<br/>
            &lt;zoehis-button type="primary" size="small" &gt;&lt;/zoehis-button&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          size2""/small/
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button type="features" icon="z_delete_normal" keyboard="0">()</zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-button type=&quot;features&quot; icon=&quot;z_delete_normal&quot; keyboard=&quot;0&quot;&gt;()&lt;/zoehis-button&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          keyboardString
        </div>
      </div>
    </div>
    <div class="item">
      <h4>/enter</h4>
      <div class="item-content">
        <zoehis-button type="features" icon="z_delete_normal" @clickenter="myClickEnterEve"></zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-button @clickenter="myClickEnterEve"&gt;&lt;/zoehis-button&gt;<br/>
            &lt;zoehis-button type="primary" @clickenter="myClickEnterEve"&gt;&lt;/zoehis-button&gt;<br/>
          </code></pre>
          <pre><code>
            //
            methods:{
              myClickEnterEve:function(evt){
                alert("");
              }
            }
          </code></pre>
        </div>
        <div class="item-desc">
          @clickenter="myClickEnterEve",/enterenter <br>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button-group>
          <zoehis-button type="features" icon="z_delete_normal"></zoehis-button><zoehis-button type="default"></zoehis-button>
        </zoehis-button-group>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-button-group&gt;
              &lt;zoehis-button type=&quot;features&quot; icon=&quot;z_delete_normal&quot;&gt;&lt;/zoehis-button&gt;
              &lt;zoehis-button type=&quot;default&quot;&gt;&lt;/zoehis-button&gt;
            &lt;/zoehis-button-group&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          zoehis-button-group
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-button type="features" :keyboard="keyboard" icon="z_delete_normal">{{featuresName}}</zoehis-button><zoehis-button type="default">{{defaultName}}</zoehis-button>
        <zoehis-button @clickenter="changeBtnCt" type="primary"></zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-button type=&quot;features&quot; :keyboard=&quot;keyboard&quot; icon=&quot;z_delete_normal&quot;&gt;{{featuresName}&lt;/zoehis-button&gt;
            &lt;zoehis-button type=&quot;default&quot;&gt;{{defaultName}&lt;/zoehis-button&gt;
          </code></pre>
          <pre><code>
            data() {
              return {
                featuresName:"",
                defaultName:"",
                keyboard:"2"
              }
            },
            methods:{
              changeBtnCt:function(evt){
                this.defaultName="";
                this.featuresName="";
                ++this.keyboard;
              }
            },
          </code></pre>
        </div>
        <div class="item-desc">
          keyboard defaultNamefeaturesName
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'button',
    data() {
      return {
        featuresName:"",
        defaultName:"",
        keyboard:"2"
      }
    },
    methods:{
      changeBtnCt:function(evt){
        this.defaultName="";
        this.featuresName="";
        ++this.keyboard;
      },
      myClickEnterEve:function(evt){
        alert("/enter");
      },
    },
    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



