<template>
  <div
    class="zoehis_select"
    v-clickoutside="handleClose"
  >
    <zoehis-input
      :width="width"
      ref="reference"
      v-model="selectedLabel"
      :placeholder="currentPlaceholder"
      :disabled="currentDisabled"
      :readonly="!filterable"
      @clear="handleClear"
      @clickicon="toggleMenu"
      @mousedown.native="handleMouseDown"
      @keyup.native="debounceOnInputChange"
      @keydown.native.down.prevent="navigateOptions('next')"
      @keydown.native.up.prevent="navigateOptions('prev')"
      @keydown.native.enter.prevent="selectOption"
      @keydown.native.tab="handleTab"
      @mouseenter.native="inputOver = true"
      @mouseleave.native="inputOver = false"
      @focus="handleFocus"
      icon="z_dropD_form"
      :close="showClose"
      clearreadonly
    >
    </zoehis-input>
    <transition
      name="zoehis_zoom_in_top"
      @after-leave="doDestroy">
    >
      <zoehis-select-dropdown
        v-show="visible"
        ref="popper"
      >
        <zoehis-scrollbar
          tag="ul"
          wrap-class="zoehis_select_dropdown__wrap"
          ref="scrollbar"
          @scrollafter="scrollafter"
        >
          <zoehis-option
            v-for="(item, index) in selectdata"
            :item="item"
            :key="item[itemkey || itemcode]"
            :value="item[itemcode]"
            :label="item[itemtext]"
            :itemindex="index"
          >
            <div v-if="(typeof renderItem=='function')" v-html="renderItem(item)"></div>
          </zoehis-option>
        </zoehis-scrollbar>
        <p class="zoehis_select_dropdown__empty" v-if="emptyText">{{ emptyText }}</p>
      </zoehis-select-dropdown>
    </transition>

  </div>
</template>

<script type="text/ecmascript-6">
  import Emitter from '../../../src/mixins/emitter';
  import ZoehisInput from '../../input/index.js'
  import ZoehisScrollbar from '../../scrollbar/index.js'
  import ZoehisOption from './option.vue'
  import ZoehisSelectDropdown from './select-dropdown.vue'
  import Clickoutside from '../../../src/utils/clickoutside.js'
  import scrollIntoView from '../../../src/utils/scroll-into-view.js'
  import { getValueByPath } from '../../../src/utils/util';
  import debounce from '../../../src/utils/throttle-debounce.js';
  export default{
    name: "ZoehisSelect",
    componentName: "ZoehisSelect",
    mixins: [Emitter],
    components: {
      ZoehisInput,
      ZoehisScrollbar,
      ZoehisOption,
      ZoehisSelectDropdown,
    },
    directives: { Clickoutside },//HTML v- 
    created: function () {
      this.debounceOnInputChange = debounce(this.debounce, (e) => {
        switch (e.keyCode){
          case 13://enter
          case 37://left
          case 38://top
          case 39://right
          case 40://down
          case 9://tab
            break;
          default:
            this.onInputChange();
        }
      });
      this.$on('handleOptionClick', this.handleOptionSelect); //
      this.$on('onOptionDestroy', this.onOptionDestroy);
      this.$on('setSelected', this.setSelected);//
    },
    methods:{
      //
      getDisabled(){
        return this.disabled;
      },
      scrollMove(num){
        this.$refs.scrollbar.move({top:num});
      },
      //
      getText(){
        return this.selectedLabel;
      },
      //
      getSelected(){
        return (this.selected && this.selected.item) ? this.selected.item : null;
      },
      //
      doDestroy() {
        this.$refs.popper && this.$refs.popper.doDestroy();
      },
      //
      focus(){
        this.$refs.reference.focus();
      },
      scrollafter(wrap){
        const [h,sh,st]=[wrap.clientHeight,wrap.scrollHeight,wrap.scrollTop];
        this.$emit("scroll-after",this.query,this);
        //
        if(sh-h-st==0){
          this.$emit("scroll-to-bottom",this.query,this);
        }
        //
        if(st==0){
          this.$emit("scroll-to-top",this.query,this);
        }
      },
      //
      initSelectData(){
        this.hoverIndex = -1;
        let option = this.getOption(this.value);//option
        if(option){
          if(typeof this.renderShowData === "function"){
            this.selectedLabel=this.renderShowData(option.item);
          }else {
            this.selectedLabel = option.item[this.itemtext];
          }
          if(option.index !== undefined){
            this.hoverIndex = option.index;
          }
          this.selected = option;
        }
      },
      //
      setSelectedLabel(val){
        this.selectedLabel=val;
      },
      //
      onOptionDestroy(option) {
        this.optionsCount--;
        this.filteredOptionsCount--;
        let index = this.options.indexOf(option);
        if (index > -1) {
          this.options.splice(index, 1);
          this.cachedOptions.splice(index, 1);
        }
      },
      //
      onInputChange() {
        //
        if (this.filterable) {
          //input""delete
          if(this.query === "" && this.selectedLabel==""){
            this.hoverIndex=-1;//
            this.$emit('input', "");//""
            this.selected="";//
            if(!this.visible && this.selectItem) {
              this.visible = true; //visibletrue,
            }
          }else {
            this.selected="";//
            this.query = this.selectedLabel;
            this.isKeyup = true;
          }
          //
          this.$nextTick(function(){
            this.$refs.scrollbar.move({top:0});
          })

        }
      },
      //
      scrollToOption(option) {
        const target = Array.isArray(option) && option[0] ? option[0].$el : option.$el;
        if (this.$refs.popper && target) {
          const menu = this.$refs.popper.$el.querySelector('.zoehis_select_dropdown__wrap');
          scrollIntoView(menu, target);
        }
      },
      //
      handleClose() {
        this.visible = false;
      },
      //
      handleFocus() {
        this.visible = true;
        this.selectItem=false;
        this.isKeyup=false;
        this.$emit('focus')
      },
      //
      handleMouseDown(event){
        if (event.target.tagName !== 'INPUT') return;
        let inputs = this.$el.querySelectorAll('input');
        //selectinput
        if ([].indexOf.call(inputs, document.activeElement) != -1) { //
          if(!this.visible){
            this.visible = true;
            this.selectItem=false;
            this.isKeyup=false;
            event.preventDefault();
          }
        }
      },
      //
      handleClear(e){
        this.deleteSelected(event);
      },
      ///
      toggleMenu() {
//        if (this.filterable && this.query === '' && this.visible) {
//          return;
//        }
        //
        if (!this.disabled) {
          this.visible = !this.visible;
        }
      },
      //tab
      handleTab(e){
        this.visible = false;
      },
      //
      navigateOptions(direction) {
        if (!this.visible) {
          this.visible = true;
          return;
        }
        //option
        if (this.options.length === 0 || this.filteredOptionsCount === 0) return;
        //
        this.optionsAllDisabled = this.options.length === this.options.filter(item => item.disabled === true).length;
        if (!this.optionsAllDisabled) {
          if (direction === 'next') {
            this.hoverIndex++;
            // hoverIndex=0
            if (this.hoverIndex === this.options.length) {
              this.hoverIndex = this.options.length-1;
            }
            //
            if (this.options[this.hoverIndex].disabled === true ||
              this.options[this.hoverIndex].groupDisabled === true ||
              !this.options[this.hoverIndex].visible) {
              this.navigateOptions('next');
            }
          }
          if (direction === 'prev') {
            this.hoverIndex--;
            //
            if (this.hoverIndex < 0) {
              this.hoverIndex = 0;
            }
            //
            if (this.options[this.hoverIndex].disabled === true ||
              this.options[this.hoverIndex].groupDisabled === true ||
              !this.options[this.hoverIndex].visible) {
              this.navigateOptions('prev');
            }
          }
        }
        this.$nextTick(() => this.scrollToOption(this.options[this.hoverIndex]));
      },
      //value option
      getOption(value) {
        let option;
        const isObject = Object.prototype.toString.call(value).toLowerCase() === '[object object]';
        for (let i = this.cachedOptions.length - 1; i >= 0; i--) {
          const cachedOption = this.cachedOptions[i];
          const isEqual = isObject
            ? getValueByPath(cachedOption.value, this.valueKey) === getValueByPath(value, this.valueKey)
            : cachedOption.value === value;
          if (isEqual) {
            option = cachedOption;
            break;
          }
        }
        if (option) return option;
        const label = !isObject
          ? value : '';
        let newOption = {
          value: value,
          currentLabel: label,
          item:{}
        };
        if (this.multiple) {
          newOption.hitState = false;
        }
        return newOption;
      },
      //
      setSelected() {
        if (!this.multiple) {//
          let option = this.getOption(this.value);//option
          if (option.created) {
            this.createdLabel = option.currentLabel;
            this.createdSelected = true;
          } else {
            this.createdSelected = false;
          }
          this.selectedLabel = option.currentLabel;
          this.selected = option;
          this.selectItem = true;
          return;
        }
        let result = [];
        if (Array.isArray(this.value)) {
          this.value.forEach(value => {
              result.push(this.getOption(value));
          });
        }
        this.selected = result;
        this.$nextTick(() => {
            this.resetInputHeight();
        });
      },
      //enteroption
      selectOption() {
        if (this.options[this.hoverIndex]) {
          this.handleOptionSelect(this.options[this.hoverIndex]);
        }
      },
      //
      deleteSelected(event) {
        event.stopPropagation();
        this.$emit('input', '');
        this.selected="";
        this.visible = false;
        this.$emit('clear');
      },
      //hoverIndex
      resetHoverIndex() {
        setTimeout(() => {
          if (!this.multiple) {
            this.hoverIndex = this.options.indexOf(this.selected);
          } else {
            // -1
            if (this.selected.length > 0) {
              this.hoverIndex = Math.min.apply(null, this.selected.map(item => this.options.indexOf(item)));
            } else {
              this.hoverIndex = -1;
            }
          }
        }, 300);
      },
      //
      handleOptionSelect(option) {
        if (this.multiple) {
          const value = this.value.slice();
          const optionIndex = this.getValueIndex(value, option.value);
          if (optionIndex > -1) {
            value.splice(optionIndex, 1);
          } else if (this.multipleLimit <= 0 || value.length < this.multipleLimit) {
            value.push(option.value);
          }
          this.$emit('input', value);
          if (option.created) {
            this.query = '';
            this.inputLength = 20;
          }
          if (this.filterable) this.$refs.input.focus();
        } else {
          this.$emit('input', option.value);
          this.visible = false;
        }
        this.$nextTick(() =>{
          this.scrollToOption(option);
          this.$emit("enter");
        });
      },
      getValueIndex(arr = [], value) {
        const isObject = Object.prototype.toString.call(value).toLowerCase() === '[object object]';
        if (!isObject) {
          return arr.indexOf(value);
        } else {
          const valueKey = this.valueKey;
          let index = -1;
          arr.some((item, i) => {
            if (getValueByPath(item, valueKey) === getValueByPath(value, valueKey)) {
              index = i;
              return true;
            }
            return false;
          });
          return index;
        }
      },
      checkDefaultFirstOption() {
        this.hoverIndex = -1;
        for (let i = 0; i !== this.options.length; ++i) {
          const option = this.options[i];
          if (this.query) {
            // pick first options that passes the filter
            if (!option.disabled && !option.groupDisabled && option.visible) {
              this.hoverIndex = i;
              break;
            }
          } else {
            // pick currently selected option
            if (option.itemSelected) {
              this.hoverIndex = i;
              break;
            }
          }
        }
      },
      //
      isNoSelectData(val){
        if(val==="" || (Array.isArray(val) || val.length===0)){
          return true;
        }else {
          return false;
        }//
      }
    },
    computed:{
      debounce() {
        return this.remote ? this.delayTime : 0;
      },
      emptyText(){
        if((this.options.length===0 || this.filteredOptionsCount === 0) && this.selectItem==false){
          if(this.realCreated){
            //
            if(this.isInit){
              this.isInit = false;
            }else {
              this.visible=true;
            }
          }
          return this.noMatchText
        }else {
          return false;
        }
      },
      //XX
      showClose(){
        let isShowClose = this.clearable &&
          this.inputOver &&
          !this.disabled &&
          this.selectedLabel!=="" &&
          this.selectedLabel!==undefined;
        return isShowClose ? true : false;
      }
    },
    mounted(){
      this.currentPlaceholder = this.placeholder;
      this.currentDisabled = this.disabled;
      this.$nextTick(() => {
        if (this.$refs.reference && this.$refs.reference.$el) {
          this.inputWidth = this.$refs.reference.$el.getBoundingClientRect().width;
        }
        this.initSelectData(this.value);
        this.realCreated = true;//
        this.isInit = true;//
      });
    },
    watch:{
      emptyText(val){
        if(!val) return;
        this.$nextTick(() => {
          this.broadcast('ZoehisSelectDropdown', 'updatePopper')
        });
      },
      //
      query(val) {
        //
        this.$nextTick(() => {
          if (!this.visible){
            if(!this.selectItem || (this.options.length && val!="")){// / 
              this.visible=true;
              this.selectItem=false;
            }else {
              this.selectItem=false;
            }
          }
          this.broadcast('ZoehisSelectDropdown', 'updatePopper')
        });
        //-1
        this.hoverIndex = -1;
        this.$emit('input', "");
        //
        if (this.multiple && this.filterable) {
          this.inputLength = this.$refs.input.value.length * 15 + 20;
          this.managePlaceholder();
          this.resetInputHeight();
        }
        if (typeof this.filterMethod === 'function') {
          this.hoverIndex = -1;
          this.filterMethod(val);
        } else { //
          this.filteredOptionsCount = this.optionsCount;
          this.broadcast('ZoehisOption', 'queryChange', val);
        }
      },
      options(val) {
        if (this.$isServer) return;
//        this.optionsAllDisabled = val.length === val.filter(item => item.disabled === true).length;
//        if (this.multiple) {
//          this.resetInputHeight();
//        }
//        let inputs = this.$el.querySelectorAll('input');
        //selectinput
//        if ([].indexOf.call(inputs, document.activeElement) === -1) {
//          this.setSelected();
//        }
      },
      value(val){
        //value
        if(this.isNoSelectData(val)){
          this.$emit("change",val,null);//change
        }else {
          this.setSelected();
          this.$emit("change",val,this.options[this.hoverIndex]?this.options[this.hoverIndex].item:null);//change
        }

      },
      visible(val) {
        if (!val) {
          this.broadcast('ZoehisSelectDropdown', 'destroyPopper'); //
//          if (this.$refs.input) {
//            this.$refs.input.blur();
//          }
//          this.query = '';
          //
          if(!this.noClearBlur){
            this.selectedLabel = '';
          }
//          this.inputLength = 20;
//          this.resetHoverIndex();
//          this.$nextTick(() => {
//            if (this.$refs.input &&
//              this.$refs.input.value === '' &&
//              this.selected.length === 0) {
//                this.currentPlaceholder = this.cachedPlaceHolder;
//              }
//          });
          if (!this.multiple) {
            if (this.selected) {
              if (this.filterable && this.allowCreate &&
                this.createdSelected && this.createdOption) {
                this.selectedLabel = this.createdLabel;
              } else {
                //
                if(typeof this.renderShowData === "function"){
                  this.selectedLabel = this.renderShowData(this.selected.item);
                }else {
                  this.selectedLabel = this.selected.currentLabel;
                }
              }
            }
            if (this.filterable) {
              this.selectItem=true;
              this.query = "";
            }
          }
        } else {
//          this.handleIconShow();

          this.broadcast('ZoehisSelectDropdown', 'updatePopper'); //
          if (this.filterable) {
//            this.query = this.selectedLabel;
//            if (this.multiple) {
//              this.$refs.input.focus();
//            } else {
//              if (!this.remote) {
//                this.broadcast('ElOption', 'queryChange', '');
//                this.broadcast('ElOptionGroup', 'queryChange');
//              }

             if(!this.isKeyup){
               this.broadcast('ZoehisInput', 'inputSelect');//
             } else {
               this.isKeyup=false;
             }
//            }
          }
          //
          this.$nextTick(() => this.scrollToOption(this.selected));
        }
        this.$emit('visible-change', val,this);
      }
    },
    data() {
      return {
        options: [],//option vue
        cachedOptions: [],//
        optionsCount: 0,//
        filteredOptionsCount: 0,//
        currentPlaceholder:"",//
        currentDisabled:true,//
        visible:false,  //
        inputOver:false,  //
        selected:this.multiple ? [] : {},
        isSelect: true,//Selectoption
        hoverIndex: -1,//
        selectedLabel:"",  //
        query: '',//
        inputWidth: 0,//
        selectItem: false,//
        isKeyup: false,//keyup
        realCreated: false,//select
        isInit: false,//
      }
    },
    props: {
      clearable: {  //XX
        type: Boolean,
        default: true
      },
      placeholder: {//
        type: String,
        default:""
      },
      disabled:Boolean,// truefalse
      filterable: Boolean,// truefalse:
      multiple: Boolean,//
      value: {
        required: true
      },
      remote: Boolean,//API
      delayTime: {    //remotetrue
        type:[String,Number],
        default:200
      },
      filterMethod: Function,//
      noMatchText: {
        default(){
          return this.$t('zoehisUI.select.noMatchText');
        },
        type:String
      },
      renderShowData:Function,
      renderItem:Function,
      scrollToBottom:Function,// 
      scrollToTop:Function,// 
      selectdata:{//
        type:Array,
        default:[]
      },
      itemkey:{ //
        type:String,
        default:""
      },
      itemcode:{ //
        type:String,
        default:"id"
      },
      itemtext:{//
        type:String,
        default:"text"
      },
      width:{//
        type:[Number,String],
        default:115
      },
      noClearBlur:{//false
        type:Boolean,
        default:false
      },
    }
  }
</script>




