<template>
  <div class="component_example">
    <h1>Upload </h1>
    <h2></h2>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-upload
          action="/upload"
          :onsuccess="handlesuccess"
          :onprogress="handleprogress"
          :onerror="handleerror"
        >
          <zoehis-button size="small"></zoehis-button>
        </zoehis-upload>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>

            &lt;zoehis-upload
              action=&quot;/upload&quot;
              :onsuccess=&quot;handlesuccess&quot;
              :onprogress=&quot;handleprogress&quot;&gt;
              :onerror=&quot;handleerror&quot;&gt;
              &lt;zoehis-button size=&quot;small&quot;&gt;&lt;/zoehis-button&gt;
            &lt;/zoehis-upload&gt;

            methods: {
              handlesuccess(res, file){
                console.log('success');
              },
              handleprogress(res, file){
                console.log('progress...');
              },
              handleerror(err,file){
                console.log('error')
              }
            }
          </code></pre>
        </div>
        <div class="item-desc">
          <h4>Attributes</h4>
          <ul>
            <li>{ String }[ action ], </li>
            <li>{ Boolean }[ multiple=true ], </li>
            <li>{ Function }[ onsuccess ], </li>
            <li>{ Function }[ onerror ], </li>
            <li>{ Function }[ progress ], </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  export default {
    methods: {
      handlesuccess(res, file){
        console.log('success');
      },
      handleprogress(res, file){
        console.log('progress...');
      },
      handleerror(err,file){
        console.log('error')
      }
    },
    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }
</script>





