<template>
  <div>

    <div class="component_example">
      <h1>Loading </h1>
      <h2></h2>

      <div class="item">
        <h4></h4>
        <div class="item-content">
          <div class="loading_test_base" v-loading="loading1">
            loadingloadingloadingloading
            loadingloadingloadingloading
          </div>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;div class=&quot;loading_test_base&quot; v-loading=&quot;loading1&quot;&gt;
              loadingloadingloadingloading
              loadingloadingloading
              loading
            &lt;/div&gt;

            &lt;script&gt;
              export default{
                data(){
                  return {
                    loading1: true
                  }
                }
              }
            &lt;/script&gt;
          </code></pre>
          </div>
          <div class="item-desc">
            v-loadingbooleantrue
            
          </div>
        </div>
      </div>

      <div class="item">
        <h4></h4>
        <div class="item-content">
          <div class="loading_test_base" v-loading="loading1"
               loading-text="" :loading-shade="true">
            loadingloadingloadingloading
            loadingloadingloading
            loading
          </div>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;div v-loading=&quot;loading1&quot; loading-text=&quot;&quot;:loading-shade="true"&gt;
              loadingloadingloadingloading
              loadingloadingloading
              loading
            &lt;/div&gt;

            &lt;script&gt;
              export default{
                data(){
                  return {
                    loading1: true
                  }
                }
              }
            &lt;/script&gt;
          </code></pre>
          </div>
          <div class="item-desc">
            v-loadingloading-textlogoloading-text
            loading-textv-loading<br><br>
            v-loading:loading-shade="true"
          </div>
        </div>
      </div>

      <div class="item">
        <h4>body</h4>
        <div class="item-content">
          <div class="loading_test_hasText" v-loading.body="loading2" loading-text="">
            loadingloadingloading
            loadingloadingloading
            loading
          </div>
          <div class="button" @click="changeLoadBody">
            /
          </div>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;div class=&quot;loading_test_hasText&quot; v-loading.body=&quot;loading2&quot;
              loading-text=&quot;&quot;&gt;
              loadingloadingloading
              loadingloadingloading
              loading
            &lt;/div&gt;
          </code></pre>
          </div>
          <div class="item-desc">
            v-load.bodybody
            
          </div>
        </div>
      </div>

      <div class="item">
        <h4></h4>
        <div class="item-content">
          <div class="button" v-loading.fullscreen.lock="fullscreenLoading"
               @click="openFullScreen" loading-text="">
            3
          </div>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;div  v-loading.fullscreen=&quot;fullscreenLoading&quot;
              @click=&quot;openFullScreen&quot; loading-text=&quot;&quot;&gt;
              3
            &lt;/div&gt;

            &lt;script&gt;
              export default{
                data(){
                  return {
                    fullscreenLoading: false
                  }
                },

                methods: {
                  openFullScreen: function () {
                    this.fullscreenLoading = true;
                    setTimeout(() =&gt; {
                      this.fullscreenLoading = false;
                    }, 3000);
                  }
                }
              }
            &lt;/script&gt;
          </code></pre>
          </div>
          <div class="item-desc">
            v-loading.fullscreenv-loading.fullscreen
            v-loadingbooleantrue
          </div>
        </div>
      </div>

      <div class="item" id="loadingId">
        <h4> this.$loading</h4>
        <div class="item-content">
          <zoehis-button @clickenter="openLoading">id=loading</zoehis-button>
          <zoehis-button @clickenter="openLoading1">body</zoehis-button>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            methods: {
              openLoading(){
                //
                let loadObj = this.$loading({
                  target:"#loadingId",//
                  fullscreen:true,//(target)
                  lock:true, //fullscreen=trueoverflowhidden
                  body:false,//body
                  text:"...(3)",//
                  customClass:"my-loading-class"//class
                  setVisible: true,//
                });
                setTimeout(()=>{
                  loadObj.close();
                },3000);
              },
              openLoading1(){
                let vThis=this;
                let loadObj2 = this.$loading({
                  fullscreen:true,//(target)true
                  lock:true, //fullscreen=trueoverflowhidden
                  body:true,//body
                });
                setTimeout(()=>{
                  loadObj2.close();
                },1000);
              }
            }
          </code></pre>
          </div>
          <div class="item-desc">
            targetdocument.querySelector <br>
            fullscreenbodybodyfullscreentrueloading <br>
            lock:true, //fullscreen=trueoverflowhidden
            ;<br>
            close()loading
          </div>
        </div>
      </div>

      <div class="item">
        <h4></h4>
        <table width="100%">
          <thead>
          <tr>
            <th></th><th></th><th></th><th></th><th></th>
          </tr>
          </thead>
          <tbody>
          <tr><td>v-loading</td><td>booleantrue</td><td></td><td>-</td><td>-</td></tr>
          <tr><td>loading-text</td><td>v-loading</td><td></td><td>-</td><td>-</td></tr>
          <tr><td>v-loading.body</td><td>bodybodyv-loading</td><td></td><td>-</td><td>-</td></tr>
          <tr><td>v-loading.fullscreen</td><td>fullscreenv-loading</td><td></td><td>-</td><td>-</td></tr>
          <tr>
            <td>target</td>
            <td>Loading  DOM  DOM  document.querySelector DOM </td>
            <td>object/string</td><td>-</td><td>document.body</td>
          </tr>
          <tr>
            <td>fullscreen</td>
            <td>(target)</td>
            <td>boolean</td><td>true/false</td><td>true</td>
          </tr>
          <tr>
            <td>lock</td>
            <td>overflowhidden</td>
            <td>boolean</td><td>true/false</td><td>false</td>
          </tr>
          <tr>
            <td>body</td>
            <td>body</td>
            <td>boolean</td><td>true/false</td><td>false</td>
          </tr>
          <tr>
            <td>text</td>
            <td></td>
            <td>string</td><td>-</td><td>-</td>
          </tr>
          <tr>
            <td>customClass</td>
            <td>class</td>
            <td>string</td><td>-</td><td>-</td>
          </tr>
          <tr>
            <td>close()</td>
            <td>loading</td>
            <td>function</td><td>-</td><td>-</td>
          </tr>
          <tr>
            <td>setVisible</td>
            <td>js</td>
            <td>boolean</td><td>true/false</td><td>false</td>
          </tr>
          <tr>
            <td>loading-shade</td>
            <td>v-loading:loading-shade="true"</td>
            <td>boolean</td><td>true/false</td><td>false</td>
          </tr>
          </tbody>
        </table>
        <div style="clear: both"></div>
      </div>

    </div>

  </div>
</template>

<script>
  export default{
    data(){
      return {
        loading1: true,
        loading2: false,
        fullscreenLoading: false
      }
    },

    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    },

    methods: {
      openLoading(){
        //
        let loadObj = this.$loading({
          target:"#loadingId",//
          fullscreen:true,//(target)
          lock:true, //fullscreen=trueoverflowhidden
          body:false,//body
          text:"...(3)",//
          customClass:"my-loading-class",//class
          setVisible: true,//
        });
        setTimeout(()=>{
          loadObj.close();
        },3000);
      },
      openLoading1(){
        let vThis=this;
        let loadObj2 = this.$loading({
          fullscreen:true,//(target)true
          lock:true, //fullscreen=trueoverflowhidden
          body:true,//body
        });
        setTimeout(()=>{
          loadObj2.close();
        },1000);
      },
      openFullScreen: function () {
        this.fullscreenLoading = true;
        setTimeout(() => {
          this.fullscreenLoading = false;
      }, 3000);
      },

      changeLoadBody: function () {
        this.loading2 = !this.loading2;
      }

    }
  }
</script>
<style>
  .button{
    line-height: 35px;
    width: 120px;
    background-color: dodgerblue;
    text-align: center;
    border-radius: 5px;
    cursor: pointer;
    color: white;
    display: inline-block;
  }
  .loading_test_base{
    width: 300px;
    height: 100px;
    border: 1px solid dodgerblue;
  }
  .loading_test_hasText{
    width: 300px;
    height: 100px;
    border: 1px solid dodgerblue;
    display: inline-block;
  }

</style>



