<template>
  <div class="zoehis_tree" :class="{ 'zoehis_tree__highlight_current': highlightcurrent }">
    <zoehis-tree-node
      v-for="child in root.childNodes"
      :render="render"
      :node="child"
      :props="props"
      :key="getNodeKey(child)"
      :rendercontent="rendercontent">
    </zoehis-tree-node>
    <div class="zoehis_tree__empty_block" v-if="!root.childNodes || root.childNodes.length===0">
      <span class="zoehis_tree__empty_text">{{ emptytext }}</span>
    </div>
  </div>
</template>

<script>
  import TreeStore from './model/tree-store';
  import emitter from '../../../src/mixins/emitter';
  import { toTree } from './model/util';
  import ZoehisTreeNode from './tree-node';

  export default {
    name: 'ZoehisTree',

    mixins: [emitter],

    components: {
      ZoehisTreeNode
    },

    props: {
      data: Array,
      highlightcurrent: {
        type: Boolean,
        default: false
      },
      nodekey: String,//
      lazy: {
        type: Boolean,
        default: false
      },
      emptytext: {
        type: String,
        default(){
          return this.$t('zoehisUI.tree.emptyText');

        }
      },
      load: Function,
      currentnodekey: [String, Number],// key
      checksrictly: Boolean,// false
      defaultcheckedkeys: Array,// key 
      defaultexpandedkeys: Array,// key 
      autoexpandparent: {//
        type: Boolean,
        default: true
      },
      expandonclicknode: {
        type: Boolean,
        default: false
      },
      defaultexpandall: Boolean,//
      filternodemethod: Function,
      rendercontent: Function,
      showcheckbox: {
        type: Boolean,
        default: false
      },
      props: {
        default() {
          return {
            children: 'children',
            label: '',
            id: 'id',
            pid: 'pid',
          };
        }
      },
      indent: {
        type: Number,
        default: 22
      },
      render: Function,
    },

    data() {
      return {
        store: null,
        root: null,
        currentNode: null,
        isShow: true
      };
    },


    computed: {
      treedata(){
        return toTree(this.data, this.props.id ? this.props.id : 'id', this.props.pid ? this.props.pid : 'pid')
      }
    },

    methods: {
      getNodeKey(node, index) {
        const nodeKey = this.nodeKey;
        if (nodeKey && node) {
          return node.data[nodeKey];
        }
        return index;
      },
      //
      getCheckedNodes(leafOnly) {
        return this.store.getCheckedNodes(leafOnly)
      },
      getCheckedKeys(leafOnly) {
        return this.store.getCheckedKeys(leafOnly);
      },
      setCheckedNodes(nodes, leafOnly) {
        if (!this.nodekey) throw new Error('[Tree] nodeKey is required in setCheckedNodes');
        this.store.setCheckedNodes(nodes, leafOnly);
      },
      setCheckedKeys(keys, leafOnly) {
        if (!this.nodekey) throw new Error('[Tree] nodeKey is required in setCheckedNodes');
        this.store.setCheckedKeys(keys, leafOnly);
      },
      appendNode(data, parentData){
        this.store.append(data, parentData)
      },
      removeNode(data){
        this.store.remove(data)
      }
    },

    created() {
      this.isTree = true;
      this.store = new TreeStore({
        key: this.nodekey,//
        data: this.treedata,
        lazy: this.lazy,
        props: {
          children: this.props.children ? this.props.children : 'children',
          label: this.props.label
        },//
        load: this.load,//
        currentNodeKey: this.currentnodekey,// key
        checkStrictly: this.checksrictly,// false
        defaultCheckedKeys: this.defaultcheckedkeys,// key 
        defaultExpandedKeys: this.defaultexpandedkeys,// key 
        autoExpandParent: this.autoexpandparent,//
        defaultExpandAll: this.defaultexpandall,//
        filterNodeMethod: this.filternodemethod// true  false 
      });
      this.root = this.store.root;
    }
  };
</script>





