import Vue from 'vue'
const isServer = Vue.prototype.$isServer;
const SPECIAL_CHARS_REGEXP = /([\:\-\_]+(.))/g;// :-_
const MOZ_HACK_REGEXP = /^moz([A-Z])/;//mozillafirefox
const ieVersion = isServer ? 0 : Number(document.documentMode);//ie
/**
 * 
 * @param string
 * @returns {string}
 */
const trim = function (string) {
  return (string || '').replace(/^[\s\uFEFF]+|[\s\uFEFF]+$/g, '');
};
/**
 * :-_foo-barfooBar
 * @param name 
 * @returns {*}
 */
const camelCase = function (name) {
  return name.replace(SPECIAL_CHARS_REGEXP, function (_, separator, letter, offset) {
    return offset ? letter.toUpperCase() : letter;//// 
  }).replace(MOZ_HACK_REGEXP, 'Moz$1'); // moz
};
/**
 * 
 * @param element 
 * @param event 
 * @param handler 
 */
export const on = (function () {
  if (!isServer && document.addEventListener) {// IE9
    return function (element, event, handler) {
      if (element && event && handler) {
        element.addEventListener(event, handler, false);
      }
    }
  } else {
    return function (element, event, handler) {
      if (element && event && handler) {
        element.attachEvent("on" + event, handler);
      }
    }
  }
})();
/**
 * 
 * @param element 
 * @param event 
 * @param handler 
 */
export const off = (function () {
  if (!isServer && document.removeEventListener) {// IE9
    return function (element, event, handler) {
      if (element && event) {
        element.removeEventListener(event, handler, false);
      }
    }
  } else {
    return function (element, event, handler) {
      if (element && event) {
        element.detachEvent("on" + event, handler);
      }
    }
  }
})();
/**
 * 
 * @param el 
 * @param event 
 * @param fn 
 */
export const once = function (element, event, handler) {
  var listener = function () {
    if (handler) {
      handler.apply(this, arguments);
    }
    off(element, event, listener);//
  }
  on(element, event, listener);
};
/**
 * 
 * @param el 
 * @param cls 
 * @returns {boolean}
 */
export const hasClass = function (element, cls) {
  if (!element || !cls) return false;
  if (cls.indexOf(" ") != -1)throw new Error('className should not contain space.');
  if (element.classList) {//IE9  h5
    return element.classList.contains(cls);
  } else {
    return (' ' + el.className + ' ').indexOf(' ' + cls + ' ') > -1;
  }
};
/**
 * 
 * @param el 
 * @param cls  
 */
export function addClass(el, cls) {
  if (!el) return;
  var curClass = el.className;
  const clss = (cls || '').split(" ");
  for (let i = 0, j = clss.length; i < j; i++) {
    var clName = clss[i];
    if (!clName)continue;
    if (el.classList) {
      el.classList.add(clName);
    } else {
      if (!hasClass(el, clName)) {
        curClass += ' ' + clName;
      }
    }
  }
  if (!el.classList) {
    el.className = curClass;
  }
};
/**
 * 
 * @param el 
 * @param cls   
 */
export function removeClass(el, cls) {
  if (!el || !cls) return;
  const clss = (cls || '').split(" ");
  var curClass = ' ' + el.className + ' ';
  for (let i = 0, j = clss.length; i < j; i++) {
    var clName = clss[i];
    if (!clName)continue;
    if (el.classList) {
      el.classList.remove(clName);
    } else {
      if (hasClass(el, clName)) {
        curClass = curClass.replace(' ' + clName + ' ', ' ');
      }
    }
  }
  if (!el.classList) {
    el.className = trim(curClass);
  }
};
/**
 * IE9,
 * @type {Function}
 * @param element 
 * @param styleName 
 */
export const getStyle = ieVersion < 9 ? function (element, styleName) {
  if (isServer) return;
  if (!element || !styleName) return null;
  styleName = camelCase(styleName);
  if (styleName === 'float') {
    styleName = 'styleFloat';
  }
  try {
    switch (styleName) {
      case 'opacity':
      {
        try {
          return element.filters.item('alpha').opacity / 100;
        } catch (e) {
          return 1.0;
        }
      }
      default:
        return (element.style[styleName] || element.currentStyle ? element.currentStyle[styleName] : null);
    }
  } catch (e) {
    return element.style[styleName];
  }
} : function (element, styleName) {
  if (isServer) return;
  if (!element || !styleName) return null;
  styleName = camelCase(styleName);
  if (styleName === 'float') {
    styleName = 'cssFloat';
  }
  try {
    var computed = document.defaultView.getComputedStyle(element, '');
    return element.style[styleName] || computed ? computed[styleName] : null;
  } catch (e) {
    return element.style[styleName];
  }
};
/**
 * 
 * @param element 
 * @param styleName   jsonvalue 
 * @param value 
 */
export function setStyle(element, styleName, value) {
  if (!element || !styleName)return;
  if (typeof styleName === 'object') {
    for (var prop in styleName) {
      if (styleName.hasOwnProperty(prop)) {//()
        setStyle(element, prop, styleName[prop]);
      }
    }
  } else {
    styleName = camelCase(styleName);
    if (styleName === 'opacity' && ieVersion < 9) {
      element.style.filter = isNaN(value) ? '' : 'alpha(opacity=' + value * 100 + ')';
    } else {
      element.style[styleName] = value;
    }
  }
};



