<template>
  <div class="component_example">
    <h1>Switch </h1>
    <h2></h2>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-switch
          v-model="value1"
          @change="change1">
        </zoehis-switch>
        <span style="margin-left: 20px"></span>
        <zoehis-switch
          v-model="value2"
          oncolor="#13ce66"
          offcolor="#dcdcdc"
          ontext="ON"
          offtext="OFF">
        </zoehis-switch>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-switch
              v-model=&quot;value1&quot;
              @change=&quot;change1&quot;&gt;
            &lt;/zoehis-switch&gt;

            &lt;zoehis-switch
              v-model=&quot;value2&quot;
              oncolor=&quot;#13ce66&quot;
              offcolor=&quot;#dcdcdc&quot;
              ontext=&quot;ON&quot;
              offtext=&quot;OFF&quot;&gt;
            &lt;/zoehis-switch&gt;

            data(){
              return {
                value1: true,
                value2: true,
              }
            },
            methods: {
              changeValue(value){
                console.log(value)
              }
            },
          </code></pre>
        </div>
        <div class="item-desc">
          <div>
            <h4>Attributes</h4>
              <ul>
                <li>{ String }[ oncolor='#00A3E7' ]switch </li>
                <li>{ String }[ offcolor='#FFF' ]switch </li>
                <li>{ String }[ ontext='' ]switch </li>
                <li>{ String }[ offtext='' ]switch </li>
              </ul>
            <h4>Events</h4>
            <ul>
              <li>{ Function }changeswitch </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div class="item">
      <h4> value </h4>
      <div class="item-content">
        <zoehis-switch
          v-model="value3"
          onvalue="100"
          offvalue="1"
          @change="change2"
        >
        </zoehis-switch>
        <span style="margin-left: 20px;">{{ value3 }}</span>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-switch
              v-model=&quot;value3&quot;
              onvalue=&quot;100&quot;
              offvalue=&quot;1&quot;
              @change=&quot;change2&quot;
            &gt;
            &lt;/zoehis-switch&gt;

            data(){
              return {
                value3: '100'
              }
            },
          </code></pre>
        </div>
        <div class="item-desc">
          <h4>Attributes</h4>
          <ul>
            <li>{ Boolean / String / Number }[ onvalue=true ]switch </li>
            <li>{ Boolean / String / Number }[ offvalue=false ]switch </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-switch
          v-model="value4"
          disabled>
        </zoehis-switch>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-switch
              v-model=&quot;value4&quot;
              disabled
            &lt;/zoehis-switch&gt;

            data(){
              return {
                value4: true
              }
            },
          </code></pre>
        </div>
        <div class="item-desc">
          <h4>Attributes</h4>
          <ul>
            <li>{ Boolean }[ disabled=false ]</li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        value1: true,
        value2: true,
        value3: '100',
        value4: true,
      }
    },
    methods: {
      change1(value){
        console.log(value)
      },
      change2(value){
        console.log(value)
      }
    },
    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }
</script>



