<template>
  <div
    :class="[
      type === 'textarea' ? 'zoehis_textarea' : 'zoehis_input',
      {
        'zoehis_over':(mouseover || isFocus),
        'zoehis_disabled':disabled,
        'zoehis_line':isShowLine,
      }
    ]"
    @click="handleClick"
    :style="textStyle"
  >
    <i
      v-if="showIcon"
      class="zoeIconfont"
      :class="iconClass"
      @click.stop="handleClickIcon"
      @mouseover="mouseover = true"
      @mouseout="mouseover = false"
    ></i>
    <input
      v-if="type!=='textarea'"
      class="zoehis_input_inner"
      type="text"
      ref="input"
      :placeholder="isShowLine ? '' : placeholder"
      :disabled="disabled"
      :readonly="readonly"
      v-on="{focus:handleFocus,blur:handleBlur}"
      @mouseover="mouseover = true"
      @mouseout="mouseover = false"
      @input="handleInput"
      @keyup.46="handleClear"
      v-model="currentValue"

    />
    <textarea
      v-else
      class="zoehis_textarea_inner"
      type="textarea"
      ref="textarea"
      :placeholder="disabled ? '' : placeholder"
      :disabled="disabled"
      :readonly="readonly"
      v-on="{focus:handleFocus,blur:handleBlur}"
      @mouseover="mouseover = true"
      @mouseout="mouseover = false"
      @input="handleInput"
      @keyup.46="handleClear"
      v-model="currentValue"
    ></textarea>
  </div>
</template>
<script>
  export default {
    name: 'ZoehisInput',
    componentName: 'ZoehisInput',
    props: {
      type: {    //
        type: String,
        default: "text"
      },
      value: {
        default:""
      }, //v-model
      icon: String,
      disabled: Boolean,
      readonly: Boolean,//truefalse
      close: {      //
        type: Boolean,
        default: false
      },
      clearreadonly: {      //
        type: Boolean,
        default: false
      },
      placeholder: String,
      width: {      //
        type:[Number,String],
        default: 115
      },
      height: {      //height
        type:[Number,String],
        default: ""
      }
    },
    data(){
      return{
        currentValue:"",//
        mouseover:false,//
        isFocus:false,//
      }
    },
    computed:{
      isShowLine:function(){
        return (this.disabled && !this.currentValue) ? true : false;
      },
      textStyle(){
        var re = /^[0-9]+\.?[0-9]*$/; //
        return {
          width: re.test(this.width) ? this.width+'px' : this.width,
          height: re.test(this.height) ? this.height+'px' : this.height
        }
      },
      showIcon(){
        //close
        if((this.close && !this.disabled) || (this.icon && !this.disabled) || this.type === 'textarea'){
          return true;
        }else {
          return false;
        }
      },
      iconClass(){
        let html = '';
        if(this.type === 'textarea'){
          html = 'zoehis_textarea_icon z_close_normal'
        }else {
          html = 'zoehis_input_icon ';
          if(this.close){
            html += 'zoehis_close z_close_normal';
          }else if(this.icon){
            html += this.icon;
          }
        }
        return html;
      }
    },
    watch: {
      value(val) {
        this.setValue(val);
      }
    },
    methods: {
      //
      getDisabled(){
        return this.disabled;
      },
      getValue:function(){ //
        return this.currentValue;
      },
      setValue:function(val){ //
        if(val===this.currentValue) return;
        this.currentValue=val;
        this.$emit('input', val);
      },
      handleFocus(event) {//
        this.isFocus=true;
        this.$emit('focus', event);
      },
      handleBlur(event) {//
        this.isFocus=false;
        this.$emit('blur', event);
      },
      handleInput(event) {//
        const value = event.target.value;
        this.$emit('input', value);
        this.$emit('change', value);
      },
      handleClickIcon(event) {
        if(this.close || this.type==='textarea'){
          this.handleClear(event);
        }else {
          this.$emit('clickicon', event);
        }
      },
      handleClear(event) { //
        let noClear = this.currentValue=="" ||
          (!this.clearreadonly && this.readonly) ||
          this.disabled ||
          (!this.close && this.type!=='textarea');
        if(noClear) return;
        this.$emit('input', "");
        this.$emit('change', "");
        this.$emit('clear', event);
      },
      handleClick(event) { //
        this.focus();
        this.$emit('click', event);
      },
      //
      inputSelect() {
        this.$refs.input.select();
      },
      //
      focus() {
        this.$refs[this.type!='textarea' ? 'input' : 'textarea'].focus();
      }
    },
    created() {
      this.currentValue=this.value;
      //
      this.$on('inputSelect', this.inputSelect);
    }
  };
</script>



