<template>
  <div>
    <div class="component_example">
      <h1>TimePicker </h1>
      <h2></h2>
      <div class="item">
        <h4></h4>
        <div class="item-content">
          <zoehis-time-picker ref="picker1" v-model="value1" :clearable="false" format="HH:mm" @keyup.tab.native="tabEven"></zoehis-time-picker>
          <zoehis-button @clickenter="getPickerValue"></zoehis-button>
          <zoehis-button @clickenter="setPickerValue"> 2017/6/10 13:16:16</zoehis-button>
        </div>
        <div class="item-detail">
          <div class="item-code"><pre><code>
            &lt;zoehis-time-picker ref=&quot;picker1&quot; v-model=&quot;value1&quot; format=&quot;HH:mm&quot; :clearable=&quot;false&quot;&gt;&lt;/zoehis-time-picker&gt;
            &lt;script&gt;
            data: function () {
              return {
                value1:new Date()
              };
            },
            getPickerValue(){
              console.log('v-model=value1',this.value1);
              console.log('',this.$refs.picker1.getTime("HH:mm:ss"));
              console.log('',this.$refs.picker1.getTime());
            },
            setPickerValue(){
              this.$refs.picker1.setTime(new Date());
            },
            &lt;/script&gt;
          </code></pre></div>
          <div class="item-desc">
            (1)v-modelv-model <br>
            (2) <br>
            (3)
            (4)setTime(val)/getTime()
            getTime(isDisplay),isDisplaytrue( 10:16:16)false
            (5)format "HH:mm:ss" "HH:mm:ss"  "HH:mm" 
          </div>
        </div>
      </div>
      <div class="item">
        <h4></h4>
        <div class="item-content">
          <zoehis-time-picker v-model="value2"></zoehis-time-picker>
        </div>
        <div class="item-detail">
          <div class="item-code"><pre><code>
            &lt;zoehis-time-picker v-model=&quot;value2&quot;&gt;&lt;/zoehis-time-picker&gt;
            &lt;script&gt;
            data: function () {
              return {
                value2:new Date("2016-6-6")
              };
            },
            &lt;/script&gt;
          </code></pre></div>
          <div class="item-desc">
            (1)clearable  true
          </div>
        </div>
      </div>
      <div class="item">
        <h4></h4>
        <div class="item-content">
          <zoehis-time-picker v-model="value3" :width="133" :picker-width="200" align="left"></zoehis-time-picker>
          <zoehis-time-picker v-model="value3" :width="133" :picker-width="200" align="center"></zoehis-time-picker>
          <zoehis-time-picker v-model="value3" :width="133" :picker-width="200" align="right"></zoehis-time-picker>
          <zoehis-time-picker v-model="value3" :width="133"></zoehis-time-picker>
        </div>
        <div class="item-detail">
          <div class="item-code"><pre><code>
            &lt;zoehis-time-picker v-model=&quot;value2&quot;&gt;&lt;/zoehis-time-picker&gt;
            &lt;script&gt;
            data: function () {
              return {
                value3:new Date()
              };
            },
            &lt;/script&gt;
          </code></pre></div>
          <div class="item-desc">
            :widthwidth 133<br>
            :picker-widthwidth  <br>
            :align left<br>
          </div>
        </div>
      </div>
      <div class="item">
        <h4></h4>
        <table width="100%">
          <thead>
          <tr>
            <th></th><th></th><th></th><th></th><th></th>
          </tr>
          </thead>
          <tbody>
          <tr><td>v-model="value"</td><td>time(),new Date("..."),</td><td>Date</td><td>-</td><td>-</td></tr>
          <tr><td>clearable</td><td>  true</td><td>boolean</td><td>true/false</td><td>true</td></tr>
          <tr><td>:width</td><td>widthwidth 133</td><td>number</td><td>true/false</td><td>true</td></tr>
          <tr><td>:picker-width</td><td>picker-width</td><td>number</td><td>-</td><td>-</td></tr>
          <tr><td>:align</td><td></td><td>string</td><td>left/center/right</td><td>left</td></tr>
          <tr><td>format</td><td>"HH:mm:ss"</td><td>string</td><td>-</td><td>"HH:mm:ss"</td></tr>
          <tr><td>readonly</td><td></td><td>boolean</td><td>true/false</td><td>false</td></tr>
          <tr><td>disabled</td><td></td><td>boolean</td><td>true/false</td><td>false</td></tr>
          <tr><td>popper-class</td><td>class</td><td>string</td><td>-</td><td>-</td></tr>
          <tr><td>tab-inner</td><td>tab()</td><td>boolean</td><td>true/false</td><td>false</td></tr>
          <tr><td>getTime()</td><td>getTime(isDisplay),isDisplaytrue()false</td><td>function</td><td>-</td><td>-</td></tr>
          <tr><td>setTime(time)</td><td>new Date()//2017-10-1 10:16:16  10:16:16</td><td>-</td><td></td><td>-</td></tr>
          <tr><td>@tab</td><td>tab</td><td>-</td><td></td><td>-</td></tr>
          <tr><td>@enter</td><td>enter</td><td>-</td><td></td><td>-</td></tr>
          </tbody>
        </table>
        <div style="clear: both"></div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default{
    data: function () {
      return {
        value1:new Date("2016-6-6 10:10:10"),
        value2:new Date("2016-6-6"),
        value3:new Date()
      };
    },
    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    },
    methods: {
      getPickerValue(){
        console.log('v-model=value1',this.value1);
        console.log('',this.$refs.picker1.getTime("HH:mm:ss"));
        console.log('',this.$refs.picker1.getTime());
      },
      setPickerValue(){
        this.$refs.picker1.setTime(new Date("2016-6-6 10:10:10"));
      },
      tabEven(curVal){
        console.log("tab",curVal);
      }
    }
  }
</script>



