/**
 * Created by lhn on 2017/8/7.
 */
/**http://www.jianshu.com/p/984cf10954ae 
 * https://github.com/ElemeFE/element/blob/dev/src/utils/resize-event.js**/
const isServer = typeof window === "undefined";//vue
/**
 * 
 */
const requestFrame = (function () {
  if (isServer)return;
  const raf = window.requestAnimationFrame || window.mozRequestAnimationFrame || window.webkitRequestAnimationFrame || function (fn) {
      return window.setTimeout(fn, 20);
    };
  return function (fn) {
    return raf(fn);
  };
})();
/**
 * 
 */
const cancelFrame = (function () {
  if (isServer)return;
  const caf = window.cancelAnimationFrame || window.mozCancelAnimationFrame || window.webkitCancelAnimationFrame || window.clearTimeout;
  return function (id) {
    return caf(id);
  }
})();
/**
 *
 * resize
 * https://zhuanlan.zhihu.com/p/24887312 scrollresize
 * @param element 
 */
const resetTrigger = function (element) {
  const trigger = element.__resizeTrigger__;// 
  const expand = trigger.firstElementChild;// 
  const contract = trigger.lastElementChild;// 
  const expandChild = expand.firstElementChild;// 
  contract.scrollLeft = contract.scrollWidth;// 
  contract.scrollTop = contract.scrollHeight;//
  expandChild.style.width = expand.offsetWidth + 1 + "px"; // 1px
  expandChild.style.height = expand.offsetHeight + 1 + 'px';  // 1px
  expand.scrollLeft = expand.scrollWidth;  // 
  expand.scrollTop = expand.scrollHeight;  // 
};
/**
 * resize
 * @param element 
 * @returns {boolean} 
 */
const checkTriggers = function (element) {
  return element.offsetWidth != element.__resizeLast__.width || element.offsetHeight != element.__resizeLast__.height;
};
/**
 * 
 * @param event 
 */
const scrollListener = function (event) {
  resetTrigger(this);// 
  if (this.__resizeRAF__)cancelFrame(this.__resizeRAF__);// 
  this.__resizeRAF__ = requestFrame(()=> {
    if (checkTriggers(this)) {// 
      this.__resizeLast__.width = this.offsetWidth;  // 
      this.__resizeLast__.height = this.offsetHeight;  // 
      this.__resizeListeners__.forEach((fn) => {  // 
        fn.call(this, event);
      });
    }
  });
};
/**
 *CSS
 */
const attachEvent = isServer ? {} : document.attachEvent;
const DOM_PREFIXES = 'Webkit Moz O ms'.split(' ');  // 
const START_EVENTS = 'webkitAnimationStart animationstart oAnimationStart MSAnimationStart'.split(' ');
const RESIZE_ANIMATION_NAME = 'resizeanim';
let animation = false;
let keyFramePrefix = '';
let animationStartEvent = 'animationstart';

if (!attachEvent && !isServer) {
  const testElement = document.createElement('fakeelement'); // 
  if (testElement.style.animationName !== undefined) {//  animation
    animation = true;
  }
  if (animation === false) {
    let prefix = '';
    for (var i = 0; i < DOM_PREFIXES.length; i++) {
      if (testElement.style[DOM_PREFIXES[i] + 'AnimationName'] !== undefined) {  // testElement
        prefix = DOM_PREFIXES[i];
        keyFramePrefix = '-' + prefix.toLowerCase() + '-';
        animationStartEvent = START_EVENTS[i];
        animation = true;
        break;
      }
    }
  }
}
;

let stylesCreated = false;
/**
 * 
 */
const createStyles = function () {
  if (!stylesCreated && !isServer) {
    const animationKeyframes = `@${keyFramePrefix}keyframes ${RESIZE_ANIMATION_NAME} { from { opacity: 0; } to { opacity: 0; } } `;
    const animationStyle = `${keyFramePrefix}animation: 1ms ${RESIZE_ANIMATION_NAME};`;
    // opacity: 0 works around a chrome bug https://code.google.com/p/chromium/issues/detail?id=286360
    // visibility: hidden  --force-accelerated-composited-scrolling bug
    const css = `${animationKeyframes}
      .resize-triggers { ${animationStyle} visibility: hidden; opacity: 0; }
      .resize-triggers, .resize-triggers > div, .contract-trigger:before { content: \" \"; display: block; position: absolute; top: 0; left: 0; height: 100%; width: 100%; overflow: hidden; z-index: -1 }
      .resize-triggers > div { background: #eee; overflow: auto; }
      .contract-trigger:before { width: 200%; height: 200%; }`;

    const head = document.head || document.getElementsByTagName('head')[0];// IE9
    const style = document.createElement('style');
    style.type = "text/css";
    if (style.styleSheet) {
      style.styleSheet.cssText = css;
    } else {
      style.appendChild(document.createTextNode(css));
    }
    head.appendChild(style);
    stylesCreated = true;
  }
};
/**
 * 
 * @param element 
 * @param fn 
 */
export const addResizeListener = function (element, fn) {
  if (isServer) return; // 
  if (attachEvent) { // ie
    element.attachEvent('onresize', fn);
  } else {
    if (!element.__resizeTrigger__) {
      if (getComputedStyle(element).position === 'static') {//css style
        element.style.position = 'relative';
      }
      createStyles();// CSS
      element.__resizeLast__ = {};// 
      element.__resizeListeners__ = [];// 
      const resizeTrigger = element.__resizeTrigger__ = document.createElement('div');// 
      resizeTrigger.className = 'resize-triggers';
      resizeTrigger.innerHTML = '<div class="expand-trigger"><div></div></div><div class="contract-trigger"></div>';
      element.appendChild(resizeTrigger);// 
      resetTrigger(element); // 
      element.addEventListener('scroll', scrollListener, true);// 
      //CSS
      if (animationStartEvent) {
        resizeTrigger.addEventListener(animationStartEvent, function (event) {// 
          if (event.animationName === RESIZE_ANIMATION_NAME) { // 
            resetTrigger(element);// 
          }
        });
      }
    }
    element.__resizeListeners__.push(fn);// 
  }
};
/**
 * 
 * @param element 
 * @param fn 
 */
export const removeResizeListener = function (element, fn) {
  if (attachEvent) { // ie
    element.detachEvent('onresize', fn);
  } else {
    element.__resizeListeners__.splice(element.__resizeListeners__.indexOf(fn), 1);// 
    if (!element.__resizeListeners__.length) {  // 
      element.removeEventListener('scroll', scrollListener);// 
      element.__resizeTrigger__ = !element.removeChild(element.__resizeTrigger__);// 
    }
  }
}





