<template>
  <div>

    <div class="component_example">
      <h1>Page </h1>
      <h2></h2>

      <div class="item">
        <h4></h4>
        <div class="item-content">
          <zoehis-page :stripCount="stripCount">
          </zoehis-page>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;zoehis-page :stripCount=&quot;stripCount&quot;&gt;&lt;/zoehis-page&gt;

            &lt;script&gt;
              export default{
                data: function () {
                  return {
                    stripCount: 40,  //
                  }
                }
              }
            &lt;/script&gt;
          </code></pre>
          </div>
          <div class="item-desc">
            zoehis-pagestripCountnumber
          </div>
        </div>
      </div>

      <div class="item">
        <h4></h4>
        <div class="item-content">
          <zoehis-page :stripArr="stripArr" :stripCount="stripCount"
                       :set_strip="setStrip" :current_page="2">
          </zoehis-page>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;zoehis-page :stripArr=&quot;stripArr&quot; :stripCount=&quot;stripCount&quot;
              :set_strip=&quot;setStrip&quot; :current_page=&quot;2&quot; &gt;
            &lt;/zoehis-page&gt;

            &lt;script&gt;
              export default{
                data: function () {
                  return {
                    stripArr: [5,10,15],  //
                    stripCount: 40,  //
                    setStrip: 5,  //
                  }
                }
              }
            &lt;/script&gt;
          </code></pre>
          </div>
          <div class="item-desc">
            zoehis-page<br><br>
            1.stripArr,<br>
            [10,20,30,40]<br><br>
            2.current_page<br>
            1<br><br>
            3.set_strip<br>
            10
          </div>
        </div>
      </div>

      <div class="item">
        <h4></h4>
        <div class="item-content">
          <zoehis-page :stripCount="stripCount" :set_strip="setStrip"
                       :isSetStrip="false">
          </zoehis-page>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;zoehis-page :stripCount=&quot;stripCount&quot; :set_strip=&quot;setStrip&quot;
              :isSetStrip=&quot;false&quot;&gt;
            &lt;/zoehis-page&gt;
          </code></pre>
          </div>
          <div class="item-desc">
            isSetStripboolean,false<br><br>
            
          </div>
        </div>
      </div>

      <div class="item">
        <h4></h4>
        <div class="item-content">
          <zoehis-page :stripArr="stripArr" :stripCount="stripCount"
                       :set_strip="setStrip"
                       @changeStrip="changeStrip" @changePage="changePage">
          </zoehis-page>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;zoehis-page :stripArr=&quot;stripArr&quot; :stripCount=&quot;stripCount&quot;
              :set_strip=&quot;setStrip&quot;
              @changeStrip=&quot;changeStrip&quot; @changePage=&quot;changePage&quot;&gt;
            &lt;/zoehis-page&gt;

            methods: {
              changeStrip: function (strip) {
                console.log(&#x27;&#x27;+strip+&#x27;&#x27;);
              },

              changePage: function (page) {
                console.log(&#x27;&#x27;+page+&#x27;&#x27;);
              }

            }
          </code></pre>
          </div>
          <div class="item-desc">
            changeStripchangePage<br><br>
            1.@changeStrip
            <br><br>
            2.@changePage
            

          </div>
        </div>
      </div>

      <div class="item">
        <h4></h4>
        <table width="100%">
          <thead>
          <tr>
            <th></th><th></th><th></th><th></th><th></th>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td>:stripArr</td>
            <td>stripArr,</td>
            <td></td><td>-</td><td>[10,20,30,40]</td>
          </tr>
          <tr>
            <td>:stripCount</td><td>stripCount</td><td>num</td><td>-</td><td>-</td>
          </tr>
          <tr>
            <td>:set_strip</td><td>set_strip</td><td>num</td><td>-</td><td>10</td>
          </tr>
          <tr>
            <td>:current_page</td><td>current_page</td><td>num</td><td>-</td><td>1</td>
          </tr>
          <tr>
            <td>:isSetStrip</td><td>isSetStripboolean,false</td><td>boolean</td><td>-</td><td>true</td>
          </tr>
          <tr>
            <td>@changeStrip</td><td>@changeStrip</td><td></td><td>-</td><td></td>
          </tr>
          <tr>
            <td>@changePage</td><td>@changePage</td><td></td><td>-</td><td></td>
          </tr>
          </tbody>
        </table>
        <div style="clear: both"></div>
      </div>

    </div>
  </div>
</template>

<script>
  export default{
    data: function () {
      return {
        stripArr: [5,10,15],  //
        stripCount: 40,  //
        setStrip: 5,  //
      }
    },

    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    },

    methods: {
      changeStrip: function (strip) {
        console.log(''+strip+'');
      },

      changePage: function (page) {
        console.log(''+page+'');
      }

    }
  }
</script>



