<template>
  <label
    class="zoehis_checkbox"
    :class="isFocus ? 'zoehis_focus' : ''"
  >
    <span
      class="zoehis_checkbox_input zoeIconfont"
      :class="[
      isChecked ? 'z_checkboxS_form' : 'z_checkbox_form',
      {
        'zoehis_disabled':disabled
      }]"
    >
      <input
        type="checkbox"
        class="zoehis_checkbox_original"
        :disabled="disabled"
        @focus="handleFocus"
        @blur="isFocus = false"
        @keyup.enter="handleEnter"
        :name="name"
        :value="label"
        v-model="model"
        ref="input"
      >
    </span>
    <span v-if="$slots.default || label">
      <slot></slot>
      <template v-if="!$slots.default">{{label}}</template>
    </span>
  </label>
</template>
<script>
  import Emitter from '../../../src/mixins/emitter.js';
  export default {
    name: 'ZoehisCheckbox',
    componentName: 'ZoehisCheckbox',
    mixins: [Emitter],
    props: {
      value: {},
      label: {},
      disabled:Boolean,//
      checked: Boolean,//
      name: String
    },
    data(){
      return{
        isFocus:false,         //
        selfModel: false,
      }
    },
    computed:{
      model: {
        get() {
          return this.isGroup
            ? this.store : this.value !== undefined
            ? this.value : this.selfModel;
        },
        set(val) {
          if (this.isGroup) {
            this.dispatch('ZoehisCheckboxGroup', 'input', [val]);//
          } else {
            this.$emit('input', val);
            this.selfModel = val;
          }
        }
      },
      isGroup() {//
        let parent = this.$parent;
        while (parent) {
          if (parent.$options.componentName !== 'ZoehisCheckboxGroup') {
            parent = parent.$parent;
          } else {
            this._checkboxGroup = parent;
            return true;
          }
        }
        return false;
      },
      isChecked() {//
        if ({}.toString.call(this.model) === '[object Boolean]') {//modelBoolean
          return this.model;
        } else if (Array.isArray(this.model)) {//
          return this.model.indexOf(this.label) > -1;
        }
      },
      store() {//
        return this._checkboxGroup ? this._checkboxGroup.value : this.value;
      }
    },
    methods: {
      //
      getDisabled(){
        return this.disabled;
      },
      focus:function(){//
        this.$refs.input.focus();
      },
      handleEnter:function(evt){//enter
        this.$emit("enter",evt);
      },
      //
      handleFocus(){
        this.isFocus = true;
        this.$emit("focus");
      },
      addToStore() {//
        if (
          Array.isArray(this.model) &&
          this.model.indexOf(this.label) === -1
        ) {
          this.model.push(this.label);
        }else {
          this.model=true;
        }
      }
    },
    created(){
      this.checked && this.addToStore();
    }
  };
</script>



