<template>
  <div class="component_example">
    <h1>Checkbox </h1>
    <h2></h2>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-checkbox v-model="checked"></zoehis-checkbox><zoehis-checkbox ref="input2" v-model="checked1" checked></zoehis-checkbox>
      </div>
      <div class="item-detail">
        <div class="item-code"><pre><code>
          &lt;zoehis-checkbox v-model=&quot;checked&quot;&gt;&lt;/zoehis-checkbox&gt;
          &lt;zoehis-checkbox v-model=&quot;checked1&quot; checked&gt;&lt;/zoehis-checkbox&gt;
          &lt;script&gt;
          data () {
            return {
              checked:true,//true false
              checked1:false
            }
          }
          &lt;/script&gt;
        </code></pre></div>
        <div class="item-desc">
          v-model="checked";v-modelcheckeddata <br>
           checked
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-checkbox v-model="checked2" disabled></zoehis-checkbox><zoehis-checkbox v-model="checked3" disabled></zoehis-checkbox>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-checkbox v-model=&quot;checked2&quot; disabled&gt;&lt;/zoehis-checkbox&gt;
            &lt;zoehis-checkbox v-model=&quot;checked3&quot; disabled&gt;&lt;/zoehis-checkbox&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          disabled
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-checkbox v-model="checked4" ref="input3"></zoehis-checkbox><zoehis-button @clickenter="setFocus"></zoehis-button>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-checkbox v-model=&quot;checked4&quot; ref=&quot;input3&quot;&gt;&lt;/zoehis-checkbox&gt;
            &lt;zoehis-button @clickenter=&quot;setFocus&quot;&gt;&lt;/zoehis-button&gt;
          </code></pre>
          <pre><code>
             methods:{
               setFocus(val){//
                this.$refs.input3.focus();
               }
             }
          </code></pre>
        </div>
        <div class="item-desc">
          focus
          this.$refs.input3.focus();
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-checkbox v-model="checked5" label="label"></zoehis-checkbox>
        <zoehis-checkbox v-model="checked6"></zoehis-checkbox>
        <zoehis-checkbox v-model="checked7" label="label"></zoehis-checkbox>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-checkbox v-model=&quot;checked5&quot; label=&quot;label&quot;&gt;&lt;/zoehis-checkbox&gt;
            &lt;zoehis-checkbox v-model=&quot;checked6&quot;&gt;&lt;/zoehis-checkbox&gt;
            &lt;zoehis-checkbox v-model=&quot;checked7&quot; label=&quot;label&quot;&gt;&lt;/zoehis-checkbox&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          1.label<br>
          2.zoehis-checkbox <br>
          3.
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-checkbox @input="myInput">D</zoehis-checkbox>
        <br>
        <zoehis-input ref="textInput"></zoehis-input>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-checkbox @input=&quot;myInput&quot;&gt;D&lt;/zoehis-checkbox&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          1.input <br>
          2.input
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <div class="item-content">
        <zoehis-checkbox-group v-model="checkboxList" @input="myInput">
          <zoehis-checkbox label="1" @input="myInput1">A</zoehis-checkbox>
          <zoehis-checkbox label="B"></zoehis-checkbox>
          <zoehis-checkbox label="C"></zoehis-checkbox>
          <zoehis-checkbox label="D" disabled></zoehis-checkbox>
        </zoehis-checkbox-group>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-checkbox-group v-model=&quot;checkboxList&quot; @input=&quot;myInput&quot;&gt;
              &lt;zoehis-checkbox label=&quot;1&quot; @input=&quot;myInput1&quot;&gt;A&lt;/zoehis-checkbox&gt;
              &lt;zoehis-checkbox label=&quot;B&quot;&gt;&lt;/zoehis-checkbox&gt;
              &lt;zoehis-checkbox label=&quot;C&quot;&gt;&lt;/zoehis-checkbox&gt;
              &lt;zoehis-checkbox label=&quot;D&quot; disabled&gt;&lt;/zoehis-checkbox&gt;
            &lt;/zoehis-checkbox-group&gt;
          </code></pre>
          <pre><code>
            data () {
              return {
                checkboxList:["B","B"]
              }
            },
          </code></pre>
        </div>
        <div class="item-desc">
          v-model="checkboxList";checkboxList
        </div>
      </div>
    </div>
    <div class="item">
      <h4></h4>
      <table width="100%">
        <thead>
        <tr>
          <th></th><th></th><th></th><th></th><th></th>
        </tr>
        </thead>
        <tbody>
        <tr><td>v-model='value'</td><td>check*</td><td>-</td><td>-</td><td>-</td></tr>
        <tr><td>checked</td><td>check()</td><td>boolean</td><td>true/false</td><td>true</td></tr>
        <tr><td>disabled</td><td></td><td>boolean</td><td>true/false</td><td>false</td></tr>
        <tr><td>readonly</td><td></td><td>boolean</td><td>true/false</td><td>false</td></tr>
        <tr><td>@input</td><td>check</td><td>-</td><td>-</td><td>-</td></tr>
        <tr><td>focus()</td><td></td><td>-</td><td>-</td><td>-</td></tr>
        </tbody>
      </table>
      <div style="clear: both"></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'checkbox',
    data () {
      return {
        checked:true,
        checked1:false,
        checked2:false,
        checked3:false,
        checked4:false,
        checked5:false,
        checked6:false,
        checked7:false,
        checkboxList:["B","D"]
      }
    },
    methods:{
      setFocus(val){//
        this.$refs.input3.focus();
      },
      myInput(val){
        this.$refs.textInput.setValue("input"+val);
      },
      myInput1(val){
        console.log(1)
      }
    },
    mounted:function() { //
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



